import Utilities from "./Utilities";

export default class Configuration {

  constructor(configObject) {

    window.CookieConsent.buffer = {
      appendChild: [],
      insertBefore: []
    }

    // Wrapper filter function
    window.CookieConsent.wrapper = function () { };

    // Settings injector for users
    window.CookieConsent.setConfiguration = this.setConfiguration.bind(this);

    window.CookieConsent.config = {
      active: true,
      cookieExists: false,
      cookieVersion: 1,
      modalMainTextMoreLink: null,
      barTimeout: 1000,
      showRejectAllButton: true,
      debug: false,
      reloadOnSave: true,
      theme: {
        barColor: '#2C7CBF',
        barTextColor: '#FFF',
        barMainButtonColor: '#FFF',
        barMainButtonTextColor: '#2C7CBF',
        modalMainButtonColor: '#4285F4',
        modalMainButtonTextColor: '#FFF',
      },
      language: {
        current: 'en',
        locale: {
          en: {
            barMainText: 'This website uses cookies to ensure you get the best experience on our website.',
            barLinkSetting: 'Cookie Settings',
            barBtnAcceptAll: 'Accept all cookies',
            barBtnRejectAll: 'Reject all cookies',
            modalMainTitle: 'Cookie settings',
            modalMainText: 'Cookies are small piece of data sent from a website and stored on the user\'s computer by the user\'s web browser while the user is browsing. Your browser stores each message in a small file, called cookie. When you request another page from the server, your browser sends the cookie back to the server. Cookies were designed to be a reliable mechanism for websites to remember information or to record the user\'s browsing activity.',
            modalBtnSave: 'Save current settings',
            modalBtnAcceptAll: 'Accept all cookies and close',
            modalBtnRejectAll: 'Reject all cookies and close',
            modalAffectedSolutions: 'Affected solutions:',
            learnMore: 'Learn More',
            on: 'On',
            off: 'Off',
          },
          cs: {
            barMainText: 'Tato strnka pro svj provoz vyuv cookies.',
            barLinkSetting: 'Nastaven cookies',
            barBtnAcceptAll: 'Povolit vechny cookies',
            barBtnRejectAll: 'Odmtnout vechny cookies',
            modalMainTitle: 'Nastaven cookies',
            modalMainText: 'Cookies jsou mal data odeslan z webov strnky a uloen v potai uivatele webovm prohleem uivatele, kdy uivatel prochz. V prohle ukld kadou zprvu do malho souboru, kter se nazv cookie. Kdy si ze serveru vydte dal strnku, v prohle odele cookie zpt na server. Soubory cookie byly navreny tak, aby byly spolehlivm mechanismem pro webov strnky, aby si pamatovaly informace nebo zaznamenvaly aktivitu uivatele pi prohlen.',
            modalBtnSave: 'Uloit nastaven',
            modalBtnAcceptAll: 'Povolit vechny cookies a zavt',
            modalBtnRejectAll: 'Odmtnout vechny cookies a zavt',
            modalAffectedSolutions: 'Nastaven cookies bude mt vliv na tyto sluby:',
            learnMore: 'Vce informac',
            on: 'Ano',
            off: 'Ne',
          },
          hu: {
            barMainText: 'Ez a weboldal Stiket hasznl a jobb felhasznli lmny rdekben.',
            barLinkSetting: 'Sti belltsok',
            barBtnAcceptAll: 'Minden sti elfogadsa',
            modalMainTitle: 'Sti belltsok',
            modalMainText: 'A HTTP-sti (ltalban egyszeren sti, illetve angolul cookie) egy informcicsomag, amelyet a szerver kld a webbngsznek, majd a bngsz visszakld a szervernek minden, a szerver fel irnytott krs alkalmval. Amikor egy weboldalt krnk le a szervertl, akkor a bngsz elkldi a szmra elrhet stiket. A sti-ket gy terveztk, hogy megbzhat mechanizmust biztostsanak a webhelyek szmra az informcik megrzsre vagy a felhasznlk bngszsi tevkenysgnek rgztsre.',
            modalBtnSave: 'Belltsok mentse',
            modalBtnAcceptAll: 'Minden Sti elfogadsa',
            modalAffectedSolutions: 'Mire lesz ez hatssal:',
            learnMore: 'Tudj meg tbbet',
            on: 'Be',
            off: 'Ki',
          }
        }
      },
      categories: {},
      services: {}
    }

    this.setConfiguration(configObject);

  }

  setConfiguration(configObject) {
    // The user overrides the default config
    console.log(window.CookieConsent.config, configObject, { ...window.CookieConsent.config, ...configObject });

    this.mergeDeep(window.CookieConsent.config, configObject)
    //loMerge(window.CookieConsent.config, configObject);
    // The cookie overrides the default and user config
    this.cookieToConfig();

    // We tell the world we did this
    Utilities.dispatchEvent(document, 'CCConfigSet');
  }

  cookieToConfig() {

    function removeReload() {
      Utilities.removeCookie();
      location.reload();
      return false;
    }

    document.cookie.split(';').filter((item) => {

      if (item.indexOf('cconsent') >= 0) {
        var cookieData = JSON.parse(item.split('=')[1]);

        // We check cookie version. If older we need to renew cookie.
        if (typeof cookieData.version === 'undefined') {
          return removeReload();
        } else {
          if (cookieData.version !== window.CookieConsent.config.cookieVersion) {
            return removeReload();
          }
        }

        // We check if cookie data categories also exist in user config
        for (let key in cookieData.categories) {

          // The cookie contains category not present in user config so we invalidate cookie
          if (typeof window.CookieConsent.config.categories[key] === 'undefined') {
            return removeReload();
          }
        }

        // We check if cookie data services also exist in user config
        cookieData.services.forEach(function (service) {

          // The cookie contains service not present in user config so we invalidate cookie
          if (typeof window.CookieConsent.config.services[service] === 'undefined') {
            return removeReload();
          }
        });

        // We we integrate cookie data into the global config object
        for (let key in cookieData.categories) {
          window.CookieConsent.config.categories[key].checked = window.CookieConsent.config.categories[key].wanted = (cookieData.categories[key].wanted === true) ? true : false;
        }

        window.CookieConsent.config.cookieExists = true;
        return true;
      }
    });

    return false;
  }


  // Simple object check.
  isObject(item) {
    return (item && typeof item === 'object' && !Array.isArray(item));
  }

  //Deep merge two objects.
  mergeDeep(target, ...sources) {
    if (!sources.length) return target;
    const source = sources.shift();

    if (this.isObject(target) && this.isObject(source)) {
      for (const key in source) {
        if (this.isObject(source[key])) {
          if (!target[key]) Object.assign(target, { [key]: {} });
          this.mergeDeep(target[key], source[key]);
        } else {
          Object.assign(target, { [key]: source[key] });
        }
      }
    }

    return this.mergeDeep(target, ...sources);
  }
}
